<template>
    <div class="g-in" style="min-height: 600px;">
        <div class="main-news-top">
            <div class="main-top-left">
                <el-breadcrumb separator-class="el-icon-arrow-right">
                    <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                    <el-breadcrumb-item to="/qa">问卷调查</el-breadcrumb-item>
                    <el-breadcrumb-item>详情</el-breadcrumb-item>
                </el-breadcrumb>
            </div>
        </div>
        <div class="main-news-container clearfix">
            <Card class="main-news-list">
                <div>
                    <p class="main-list-title">{{content.name}}</p>
                    <div class="main-list-wrap">
                        <p class="main-summary">{{content.summary}}</p>
                        <div class="qa-code clearfix">
                            <p id="qrcode">分享二维码：</p>
                        </div>
                        <p class="qa-btn">
                            <!-- <a style="color:#fff;" @click="goList">进入问卷</a> -->
                            <a style="color:#fff;" :href="`${projectName}/questionare/info?urlId=${content.urlId}`">进入问卷</a>
                            <!-- <Button type="primary"><a  style="color:#fff;">进入问卷</a></Button> -->
                        </p>
                    </div>
                </div>
            </Card>
        </div>
    </div>
</template>





<script>
    import axios from 'axios'
    import qs from 'qs'
    import QRCode from 'qrcodejs2'
    import {locker} from '../../libs/project.js'
    export default {
        data() {
            return {
                content: {},
                _id: ''
            }
        },
        async created() {
            this._id = this.$route.params.id;
            this.init()
        },
        methods: {
            qrcode() {
                // console.log('-------------->>',window.location.href.split('//')[1].split('/')[0])
                let qrcode = new QRCode('qrcode', {
                    width: 100,
                    height: 100, // 高度
                    text: 'http://'+window.location.href.split('//')[1].split('/')[0]+this.projectName + '/questionare/mobile/?urlId='+this.content.urlId+'&locker='+locker.locker, // 二维码内容
                    render: 'canvas' // 设置渲染方式（有两种方式 table和canvas，默认是canvas）
                })
            },
            async init() {
                this.getDatas();
            },
            async getDatas() {
                const res2 = await axios.post(this.projectName + '/questionare/find.do', qs.stringify({
                    id: this._id,
                    ...locker
                }));
                if (res2.data.status) {
                    return this.$Message.error(res2.data.message);
                }
                this.content = res2.data.message;
                this.qrcode()
            },
            goList() {
                this.$router.push({
                    name: 'qaDetailList'
                })
            }
        }
    }
</script>
<style lang="scss" scoped>
    .main-news-top {
        display: flex;
        .main-top-left {
            width: 100%;
            height: 48px;
            line-height: 48px;
            background-color: #fff;
            box-sizing: border-box;
            padding: 0 20px;
            margin: 20px 0 0;
            .el-breadcrumb{
                line-height: 48px;
            }
        }
        .search-container {
            position: relative;
            flex: auto;
            .search-input {
                width: 144px;
                height: 33px;
                border-radius: 4px;
                background-color: #ffffff;
                border: solid 1px #d5d5d5;
                outline: none;
                padding-left: 10px;
            }
            .search-btn {
                display: inline-block;
                width: 58px;
                height: 33px;
                border-radius: 4px;
                background-color: #00aec4;
                color: #fff;
                text-align: center;
                line-height: 33px;
            }
            .main-news-page {
                position: absolute;
                right: 0;
                top: 0;
            }
        }
    }
    .main-news-container {
        margin: 25px 0 50px; // display: flex;
        // flex-direction: column;
        // flex-wrap: wrap;
        // align-content: flex-start;
        // height: 600px;
        min-height: 433px;
        .main-news-menu {
            width: 212px; // margin-right: 30px;
            float: left;
            p {
                width: 100%;
                height: 44px;
                background-color: #f5f5f5;
                color: #9b9b9b;
                font-size: 16px;
                line-height: 44px;
                padding-left: 15px;
                cursor: pointer;
            }
            .main-news-menu-item {
                padding: 12px 15px;
                span {
                    display: inline-block;
                    height: 30px;
                    line-height: 30px;
                    width: 100%; // text-align: center;
                    padding-left: 10px;
                    cursor: pointer;
                    &:hover {
                        color: #00aec4;
                        font-weight: 700;
                    }
                }
                .menu-item-active {
                    color: #00aec4;
                    font-weight: 700;
                }
            }
        }
        .main-news-list {
            width: calc(100%);
            float: right;
            min-height: 430px; // margin-left: 242px;
            .main-list-title {
                height: 60px;
                line-height: 50px;
                text-align: center;
                font-size: 24px;
                border-bottom: 1px solid #E8E8E8;
            }
            .main-list-wrap {
                height: 80px;
                margin: 10px 0;
                padding: 0 20px;
                .main-news-item {
                    line-height: 80px;
                    span {
                        font-size: 18px;
                    }
                    .main-item-title {
                        cursor: pointer;
                        &:hover {
                            color: #003652;
                            font-weight: 700;
                        }
                    }
                    .main-item-time {
                        float: right;
                    }
                    .main-item-content {
                        text-indent: 3em;
                        font-size: 14px;
                        line-height: 25px;
                        box-sizing: border-box;
                    }
                }
            }
        }
    }
    .no-data {
        height: 60px;
        line-height: 60px;
        color: #ccc;
        text-align: center;
        font-size: 24px;
    }
    .main-page {
        text-align: right;
    }
    .main-summary {
        text-indent: 2em;
        line-height: 1.5em;
        font-size: 15px;
    }
    #qrcode {
        margin: 20px;
        float:right;
    }
    .qa-btn {
        margin: 0 auto;
        width: 100px;
        height: 30px;
        line-height: 30px;
        text-align: center;
        border-radius: 3px;
        background: #034766;
    }
</style>





